body {
  margin: 20px;
  font-family: Arial, sans-serif;
}

img {
  width: 500px;
}

.loader {
  width: 64px;
  height: 12px;
  background-color: rgba(0, 0, 0, 0);
  background-image: radial-gradient(6px, #fff 100%, rgba(0, 0, 0, 0) 0), radial-gradient(6px, #fff 100%, rgba(0, 0, 0, 0) 0);
  background-position: 0, 100%;
  background-repeat: no-repeat;
  background-size: 12px 12px;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  position: relative;
}

.loader:before {
  content: "";
  width: 12px;
  height: 12px;
  background: #ff3d00;
  border-radius: 50%;
  margin: auto;
  animation-name: mvX, mvY;
  animation-duration: .6s, .3s;
  animation-timing-function: cubic-bezier(.5, -300, .5, 300);
  animation-iteration-count: infinite;
  animation-direction: normal, normal;
  animation-play-state: running, running;
  animation-delay: 0s, 0s;
  animation-fill-mode: none, none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes mvX {
  100% {
    left: .85px;
  }
}

@keyframes mvY {
  100% {
    top: .5px;
  }
}

.error {
  color: red;
  font-size: 50px;
  font-weight: bold;
  display: none;
}

.breed-select {
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

.cat-info {
  margin-top: 10px;
}

.cat-info img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.cat-info p {
  margin: 0;
  font-size: 16px;
  line-height: 1.6;
  display: flex;
}

.cat-container {
  align-items: center;
  display: flex;
}

.cat-info img {
  float: left;
  margin-right: 20px;
}

.cat-info p {
  margin-top: 0;
}

